<template>
    <div class="instruments" v-bind:class="type">
        <Step1 v-if="currentStep==1" :type="type" @changeStep="changeStep"/>
        <Step2 v-if="currentStep==2" :type="type" :data="currentData" @changeStep="changeStep"/>
        <Step3 v-if="currentStep==3" :type="type" :data="currentData" @changeStep="changeStep" @clearCache="clearCache"/>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import Step1 from '@/components/instruments/Step1.vue'
import Step2 from '@/components/instruments/Step2.vue'
import Step3 from '@/components/instruments/Step3.vue'

export default defineComponent({
    props: {
        type: {
            type: String,
            default: '',
        },
    },
    components: {
        Step1, Step2, Step3
    },
    data: () => ({
        currentStep: 1,
        currentData: {}
    }),
    watch: { 
        type: function(newVal, oldVal) {
            this.currentStep = 1
            this.currentData = {}
            // this.loadFromCache()
        },
        $route(to, from) {
            if (!to.fullPath.startsWith('/cabinet/instruments')) {
                this.clearCache()
            }
        }
    },
    methods: {
        changeStep(step, data=null) {
            this.currentStep = step
            if (data) {
                this.currentData = data
                localStorage.depositClientCurrentCalculatorType = this.type
                localStorage.depositClientCurrentCalculatorStep = step
                localStorage.depositClientCurrentCalculatorData = JSON.stringify(data)
            }
            if (step==1) {
                this.clearCache()
            }
        },
        clearCache() {
            delete localStorage.depositClientCurrentCalculatorData
            delete localStorage.depositClientCurrentCalculatorStep
            delete localStorage.depositClientCurrentCalculatorType
        },
        loadFromCache() {
            if (this.type == localStorage.depositClientCurrentCalculatorType) {
                this.currentStep = localStorage.depositClientCurrentCalculatorStep ?? 1,
                this.currentData = JSON.parse(localStorage.depositClientCurrentCalculatorData) ?? {}
            } else {
                this.clearCache() 
            }
        }
    },
    mounted() {
        this.loadFromCache()
    }
})
</script>

<style scoped>
.instruments {
    /* background: #FFFFFF; */
    height: 100%;
}
</style>
