<template>
    <div class="instruments-step-1">
        <div class="window">
            <CalculatorAuto v-if="type=='auto'" @changeStep="changeStep"/>
            <CalculatorProperty v-if="type=='property'" @changeStep="changeStep"/>
            <CalculatorCredit v-if="type=='credit'" @changeStep="changeStep"/>
            <CalculatorDeposit v-if="type=='deposit'" @changeStep="changeStep"/>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import CalculatorAuto from "@/components/instruments/CalculatorAuto.vue"
import CalculatorProperty from "@/components/instruments/CalculatorProperty.vue"
import CalculatorCredit from "@/components/instruments/CalculatorCredit.vue"
import CalculatorDeposit from "@/components/instruments/CalculatorDeposit.vue"

export default defineComponent({
    props: [
        'type'
    ],
    components: {
        CalculatorAuto, CalculatorProperty, CalculatorCredit, CalculatorDeposit
    },
    methods: {
        changeStep(step, data) {
            this.$emit('changeStep', step, data)
        }
    }
})
</script>

