<template>
    <div class="instruments-contract">
        <p class="instruments-contract-title">{{$localizeFilter('calculator_3_text_1')}}</p>

        <p class="instruments-text title">{{$localizeFilter('contract_amount')}}</p>
        <p class="instruments-text text">{{amount}} dream</p>

        <p class="instruments-text title">{{$localizeFilter('calculator_3_text_3')}}</p>
        <p class="instruments-text text">{{type=='credit' ? $dateFilter(data.wishedEndTimestamp, 'date') : (monthsCount + ' ' + $localizeFilter('months'))}}</p>

        <p class="instruments-text title">{{$localizeFilter('payouts')}}</p>
        <p class="instruments-text text">{{$localizeFilter('in_end_of_term')}}</p>

        <p class="instruments-text title">{{$localizeFilter('calculator_3_text_5')}}</p>
        <!-- <p class="instruments-text text">{{monthFee}} dream</p> -->
        <p class="instruments-text text">{{amount}} dream</p>

        <p class="instruments-text title">{{$localizeFilter('calculator_3_text_6')}}</p>
        <p class="instruments-text text">{{fee}} dream</p>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: ['type', 'data'],
    setup() {
        
    },
    data: ()=>({
        amount: 0,
        monthsCount: 0,
        endTimestamp: 0,
        fee: 0,
        // monthFee: 0
    }),
     mounted() {
        // console.log(this.data)
        const {amount, clientPaymentPercent:percent, monthsCount, endTimestamp} = this.data
        this.amount = amount
        this.fee = (percent*amount).toFixed(2)
        // this.monthFee = (amount / monthsCount).toFixed(2)
        this.monthsCount = monthsCount,
        this.endTimestamp = endTimestamp
    }
})
</script>

