<template>
    <div class="instrument-date">
        <div class="date-header">{{headerText}}</div>
        <div class="date-content">
            <img class="calendar" src="@/assets/newImages/calendar.png">
            <p class="date">{{$dateFilter(endTimestamp, 'date')}}</p>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    props: [
        'type', 'data'
    ],
    data: () => ({
        endTimestamp: 0
    }),
    setup() {
        
    },
    mounted() {
        const {endTimestamp} = this.data
        this.endTimestamp = endTimestamp
    },
    computed: {
        headerText() {
            switch (this.type) {
                case 'auto':
                    return localizeFilter('calculator_auto_2_text_3')
                case 'property':
                    return localizeFilter('calculator_property_2_text_5')
                case 'credit':
                    return localizeFilter('calculator_credit_2_text_3')
                case 'deposit':
                    return localizeFilter('calculator_deposit_2_text_3')
            } 
            return ''
        }
    }
})
</script>


<style scoped>
.instrument-date {
display: flex;
flex-direction: column;
align-items: flex-start;
justify-content: center
}

.date-header {
    font-size: 18px;
    line-height: 21px;
    color: #000000;
    margin: 12px 12px 12px 12px;
}

.date-content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-bottom: 8px;
}

.calendar {
    height: 24px;
    width: 26px;
}

.date {
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    color: #000000;
    margin: 0 0 0 8px;
}
</style>