<template>
    <div class="calculator">
         <p class="window-title">{{$localizeFilter('calculator')}}</p>
        <p class="window-text text">{{$localizeFilter('calculator_auto_1_text')}}</p>
        <div class="input-field">
            <label for="mark" class="custom-input-label">{{ $localizeFilter('calculator_auto_1_field_1') }}</label>
            <input id="mark" class="custom-input" type="text" v-model.trim="mark" :class="{ invalid: v$.mark.$error }"/>
            <small class="error" v-for="(error, index) of v$.mark.$errors" :key="index">
                
                {{ error.$message }}
            </small>
        </div>

        <div class="input-field">
            <label for="model" class="custom-input-label">{{ $localizeFilter('calculator_auto_1_field_2') }}</label>
            <input id="model" class="custom-input" type="text" v-model.trim="model" :class="{ invalid: v$.model.$error }"/>
            <small class="error" v-for="(error, index) of v$.model.$errors" :key="index">
                
                {{ error.$message }}
            </small>
        </div>

         <div class="input-field">
            <label for="year" class="custom-input-label">{{ $localizeFilter('calculator_auto_1_field_3') }}</label>
            <input id="year" class="custom-input" type="text" v-model.trim="year" :class="{ invalid: v$.year.$error }"/>
            <small class="error" v-for="(error, index) of v$.year.$errors" :key="index">
                
                {{ error.$message }}
            </small>
        </div>

        <div class="calculator-slider-block">
            <Toggle :title="$localizeFilter('state')" :variants="states" @toggle="toggle" />
        </div>

        <div class="input-field">
            <label for="amount" class="custom-input-label">{{ $localizeFilter('amount_usd') }}</label>
            <input id="amount" class="custom-input" type="text" v-model.trim="amount" :class="{ invalid: v$.amount.$error }"/>
            <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                
                {{ error.$message }}
            </small>
        </div>
         
        
        <div class="place-for-button">
            <button class="dark-button" @click="submitHandler">{{$localizeFilter('calculate')}}</button>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { required, numeric, helpers } from "@vuelidate/validators";
import Toggle from "@/components/Toggle.vue"
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    components: {
        Toggle
    },
    data: () => ({
        mark: "",
        model: "",
        year: "",
        amount: "",
        states: ['state_new','state_used'],
        selectedState: 'state_new'
    }),
     setup() {
        return { v$: useVuelidate() };
    },
    validations() {
    return {
      mark: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      model: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      year: {
        required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
        numeric: helpers.withMessage(localizeFilter('field_must_be_numeric'), numeric)
      },
      amount: {
        required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
        numeric: helpers.withMessage(localizeFilter('field_must_be_numeric'), numeric)
     }
    };
  },
  methods: {
      async submitHandler() {
        this.v$.$touch();
        if (this.v$.$error) {
            return;
        }
        const {clientPaymentPercent, monthsCount, endTimestamp} = await this.$store.dispatch('getDepositTypeParameters', {type: 'auto'}) //type: ‚Äòauto‚Äô || ‚Äòtarget‚Äô || ‚ÄòrealEstate‚Äô || ‚ÄòcloseCredit‚Äô
        const data = {mark: this.mark, model: this.model, year: this.year, amount: this.amount, state: this.selectedState, clientPaymentPercent, monthsCount, endTimestamp}
        this.$emit('changeStep',2,data)
      },
      toggle(idx) {
          this.selectedState = this.states[idx]
      }
  }
})
</script>


<style scoped>
.place-for-button {
    width: 100%;
    display: flex;
    flex-direction: row-reverse;
}
.dark-button {
    padding: 8px 10px 8px 10px;
}
.input-field {
    margin-top: 28px;
}
.window-title {
    margin-top:10px;
}
.window-text {
margin-top:-16px;
    margin-bottom:30px;
}
</style>