<template>
<div class="instrument-conditions">
    <div class="conditions-header">{{$localizeFilter(headerLocale)}}</div>
    <div class="chart-wrapper">
     <div class="place-for-chart">
            <vue3-chart-js class="chart"
                v-bind="{ ...doughnutChart }"
                @before-render="beforeRenderLogic"
            >
            </vue3-chart-js>
        </div>
    </div>
    <div class="conditions-legend">
        <div class="conditions-legend-mark orange"></div>
        <div class="conditions-legend-text">{{(this.percent*100).toFixed(0) + '% ' + $localizeFilter('your_fee') + ' ' + fee + ' dream(' + fee + ' ' + $localizeFilter('usd')+')'}}</div>
    </div>
     <div class="conditions-legend">
        <div class="conditions-legend-mark blue"></div>
        <div class="conditions-legend-text">{{((1-this.percent)*100).toFixed(0) + '% ' +$localizeFilter('company_fee') + ' ' +  companyFee + ' dream(' + companyFee + ' ' + $localizeFilter('usd')+')'}}</div>
    </div>
</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import Vue3ChartJs from '@j-t-mcc/vue3-chartjs'

export default defineComponent({
    props: [
        'type', 'data'
    ],
    data: ()=>({
        amount: 0,
        percent: 0,
        fee: 0,
        companyFee: 0
    }),
    setup(props) {
    const percent = props.data.clientPaymentPercent * 100.0
     const doughnutChart = {
      id: 'doughnut',
      type: 'doughnut',
      data: {
        labels: [],
        datasets: [
          {
            backgroundColor: [
              '#FF9400',
              '#0B5FA5',
            ],
            data: [percent, 100-percent]
          }
        ], 
      },
    options: {
        plugins: {
            legend: {
                display: false
            }
        },
        cutout: 80,
        borderWidth: 0
    }  
    }
    const beforeRenderLogic = (event) => {
    }
    return {
      doughnutChart,
      beforeRenderLogic
    }
    },
    components: {
        Vue3ChartJs
    },
    computed: {
        headerLocale() {
            let locale = ''
            switch (this.type) {
                case 'auto':
                    locale = 'calculator_auto_2_title_2'
                    break
                case 'property':
                     locale = 'calculator_property_2_title_2'
                     break
                case 'credit':
                     locale = 'calculator_credit_2_title_2'
                     break
                case 'deposit':
                    locale = 'calculator_deposit_2_title_2'
                    break
            } 
            return locale
        }
    },
     mounted() {
        const {amount, clientPaymentPercent:percent} = this.data
        this.amount = amount
        this.percent = percent
        this.fee = (this.amount * this.percent).toFixed(2)
        this.companyFee = (this.amount * (1-this.percent)).toFixed(2)
    }
})
</script>


