<template>
    <div class="calculator">
         <p class="window-title">{{$localizeFilter('calculator')}}</p>
        <p class="window-text text">{{$localizeFilter('calculator_deposit_1_text')}}</p>
        <div class="input-field">
            <label for="product" class="custom-input-label">{{$localizeFilter('calculator_deposit_1_field_1')}}</label>
            <input id="product" class="custom-input" type="text" v-model.trim="product" :class="{ invalid: v$.product.$error }"/>
            <small class="error" v-for="(error, index) of v$.product.$errors" :key="index">
                
                {{ error.$message }}
            </small>
        </div>


        <div class="input-field">
            <label for="amount" class="custom-input-label">{{ $localizeFilter('calculator_deposit_1_field_2') }}</label>
            <input id="amount" class="custom-input" type="text" v-model.trim="amount" :class="{ invalid: v$.amount.$error }"/>
            <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                
                {{ error.$message }}
            </small>
        </div>

        <div class="view-for-radiobuttons">
            <div class="radio-title">{{$localizeFilter('deposit_radio_title')}}</div>
            <div class="radiobutton-block" @click="selectMonth(4)">
                <input type="radio" id="radio1" class="radiobutton" value="4" v-model="selectedMonth">
                <label for="radio1" class="radio-label">{{$localizeFilter('through') + ' ' + months[0] + ' ' + $localizeFilter('months_1')}}</label>
            </div>
            <div class="radiobutton-block" @click="selectMonth(5)">
                <input type="radio" id="radio2" value="5" class="radiobutton" v-model="selectedMonth">
                <label for="radio2" class="radio-label">{{$localizeFilter('through') + ' ' + months[1] + ' ' + $localizeFilter('months_2')}}</label>
            </div>
            <div class="radiobutton-block" @click="selectMonth(7)">
                <input type="radio" id="radio3" value="7" class="radiobutton" v-model="selectedMonth">
                <label for="radio3" class="radio-label">{{$localizeFilter('through') + ' ' + months[2] + ' ' + $localizeFilter('months_2')}}</label>
            </div>
        </div>
        
        <div class="place-for-button">
            <button class="dark-button" @click="submitHandler">{{$localizeFilter('calculate')}}</button>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { required, numeric, helpers } from "@vuelidate/validators";
import localizeFilter from "@/filters/localize.filter";

export default defineComponent({
    data: () => ({
        product: "",
        amount: "",
        months: [4,5,7],
        selectedMonth: 4
    }),
     setup() {
        return { v$: useVuelidate() };
    },
    validations() {
    return {
      product: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      amount: {
        required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
        numeric: helpers.withMessage(localizeFilter('field_must_be_numeric'), numeric)
      }
    };
  },
  methods: {
      async submitHandler() {
        this.v$.$touch();
        if (this.v$.$error) {
            return;
        }
        const {clientPaymentPercent, monthsCount, endTimestamp} = await this.$store.dispatch('getDepositTypeParameters', {type: 'target', wishedMonthsCount: this.selectedMonth}) //type: ‚Äòauto‚Äô || ‚Äòtarget‚Äô || ‚ÄòrealEstate‚Äô || ‚ÄòcloseCredit‚Äô
        const data = {product: this.product, amount: this.amount, clientPaymentPercent, monthsCount, endTimestamp, wishedMonthsCount: this.selectedMonth}
        this.$emit('changeStep',2,data)
      },
    selectMonth(value) {
        this.selectedMonth = value
    }
  }
})
</script>


<style scoped>
.place-for-button {
    width: 100%;
    display: flex;
    flex-direction: row-reverse;
}
.dark-button {
    padding: 8px 10px 8px 10px;
}
.input-field {
    margin-top: 28px;
}
.view-for-radiobuttons {
  margin-bottom: 26px;
}
.window-title {
    margin-top:10px;
}
.window-text {
margin-top:-16px;
    margin-bottom:30px;
}
</style>