<template>
    <div class="instruments-info">
        <div class="instruments-info-title-block">
            <img class="instruments-info-title-image" src="@/assets/newImages/info.png">
            <p class="instruments-info-title">{{$localizeFilter('calculator_property_2_title_1')}}</p>
        </div>

        <p class="instruments-text title">{{$localizeFilter('calculator_property_2_text_1')}}</p>
        <p class="instruments-text text">{{city + ', ' + country}}</p>

        <p class="instruments-text title">{{$localizeFilter('calculator_property_2_text_2')}}</p>
        <p class="instruments-text text">{{$localizeFilter(type)}}</p>

        <p class="instruments-text title">{{$localizeFilter('calculator_property_2_text_3')}}</p>
        <p class="instruments-text text">{{area + ' ' + $localizeFilter('meters_2')}}</p>

        <p class="instruments-text title">{{$localizeFilter('calculator_property_2_text_4')}}</p>
        <p class="instruments-text text">{{amount}} dream</p>

        <p class="instruments-text title">{{$localizeFilter('fee')}}:</p>
        <p class="instruments-text text">{{fee}} dream</p>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: ['data'],
    data: () => ({
        country: '', city: '', area: '', type:'', amount: 0, percent: 0, fee: 0
    }),
    setup() {
        
    },
    mounted() {
        const {country, city, area, type, amount, clientPaymentPercent:percent} = this.data
        this.country = country
        this.city = city
        this.area = area
        this.type = type
        this.amount = amount
        this.percent = percent
        this.fee = (amount * percent).toFixed(2)
    }
})
</script>

