<template>
    <div class="calculator">
         <p class="window-title">{{$localizeFilter('calculator')}}</p>
        <p class="window-text text">{{$localizeFilter('calculator_property_1_text')}}</p>
        <div class="input-field">
            <label for="country" class="custom-input-label">{{ $localizeFilter('calculator_property_1_field_1') }}</label>
            <input id="country" class="custom-input" type="text" v-model.trim="country" :class="{ invalid: v$.country.$error }"/>
            <small class="error" v-for="(error, index) of v$.country.$errors" :key="index">
                
                {{ error.$message }}
            </small>
        </div>

        <div class="input-field">
            <label for="city" class="custom-input-label">{{ $localizeFilter('calculator_property_1_field_2') }}</label>
            <input id="city" class="custom-input" type="text" v-model.trim="city" :class="{ invalid: v$.city.$error }"/>
            <small class="error" v-for="(error, index) of v$.city.$errors" :key="index">
                
                {{ error.$message }}
            </small>
        </div>

        <div class="calculator-slider-block">
            <Toggle :title="$localizeFilter('kind')" :variants="types" @toggle="toggle" />
        </div>

         <div class="input-field">
            <label for="area" class="custom-input-label">{{ $localizeFilter('calculator_property_1_field_3') }}</label>
            <input id="area" class="custom-input" type="text" v-model.trim="area" :class="{ invalid: v$.area.$error }"/>
            <small class="error" v-for="(error, index) of v$.area.$errors" :key="index">
                
                {{ error.$message }}
            </small>
        </div>

        <div class="input-field">
            <label for="amount" class="custom-input-label">{{ $localizeFilter('amount_usd') }}</label>
            <input id="amount" class="custom-input" type="text" v-model.trim="amount" :class="{ invalid: v$.amount.$error }"/>
            <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                
                {{ error.$message }}
            </small>
        </div>
         
        
        <div class="place-for-button">
            <button class="dark-button" @click="submitHandler">{{$localizeFilter('calculate')}}</button>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { required, numeric, helpers } from "@vuelidate/validators";
import Toggle from "@/components/Toggle.vue"
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    components: {
        Toggle
    },
    data: () => ({
        country: "",
        city: "",
        area: "",
        amount: "",
        types: ['flat', 'house'],
        selectedType: 'flat'
        
    }),
     setup() {
        return { v$: useVuelidate() };
    },
    validations() {
    return {
        country: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
        city: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
        area: {
            required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
            numeric: helpers.withMessage(localizeFilter('field_must_be_numeric'), numeric)
        },
        amount: {
            required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
            numeric: helpers.withMessage(localizeFilter('field_must_be_numeric'), numeric)
        }
    };
  },
  methods: {
      async submitHandler() {
        this.v$.$touch();
        if (this.v$.$error) {
            return;
        }
        const {clientPaymentPercent, monthsCount, endTimestamp} = await this.$store.dispatch('getDepositTypeParameters', {type: 'realEstate'}) //type: ‚Äòauto‚Äô || ‚Äòtarget‚Äô || ‚ÄòrealEstate‚Äô || ‚ÄòcloseCredit‚Äô
        const data = {country: this.country, city: this.city, area: this.area, amount: this.amount, type: this.selectedType, clientPaymentPercent, monthsCount, endTimestamp}
        this.$emit('changeStep',2,data)
      },
      toggle(idx) {
          this.selectedType = this.types[idx]
      }
  }
})
</script>


<style scoped>
.place-for-button {
    width: 100%;
    display: flex;
    flex-direction: row-reverse;
}
.dark-button {
    padding: 8px 10px 8px 10px;
}
.input-field {
    margin-top: 28px;
}
.window-title {
    margin-top:10px;
}
.window-text {
margin-top:-16px;
    margin-bottom:30px;
}
</style>