<template>
    <div class="instruments-step-3">
        <button class="back-button" @click="goBack">
            <img class="back-button-arrow" src="@/assets/newImages/black_arrow.png"/>
            <p class="back-button-text">{{$localizeFilter('calculator_3_title')}}</p>
        </button>
         <div class="window contract">
            <InstrumentContract :type="type" :data="data"/>
         </div>

        <p class="instruments-text title">{{$localizeFilter('calculator_3_text_7')}}</p>
        <p class="instruments-text text">{{fee}} dream</p>

         <div class="conditions-block" @click="accept–°onditions">
             <input type="checkbox" id="checkbox" class="checkbox" v-model="conditionsAccepted" @click="accept–°onditions">
             <!-- <label for="checkbox" class="conditions-label">{{$localizeFilter('calculator_3_agreement_1')}} <a class="link" href="#">{{$localizeFilter('calculator_3_agreement_2')}}</a></label> -->
            <label for="checkbox" class="conditions-label">{{$localizeFilter('calculator_3_agreement_1') + ' ' + $localizeFilter('calculator_3_agreement_2')}}</label>
         </div>
         
         <div class="instruments-bottom-block">
                <button class="dark-button" :class="conditionsAccepted ? '' : 'inactive'" @click="submitHandler">{{$localizeFilter('next')}}</button>
                <label class="error" v-if="notEnoughFunds">{{$localizeFilter('calculator_3_not_enough_funds')}}</label>
         </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import InstrumentContract from "@/components/instruments/InstrumentContract.vue"
import localizeFilter from '@/filters/localize.filter'


export default defineComponent({
    props: [
        'type', 'data'
    ],
    components: {
        InstrumentContract
    },
    setup() {
        
    },
    data: () => ({
        conditionsAccepted: false,
        notEnoughFunds: false,
        amount: 0,
        fee: 0
    }),
    methods: {
        accept–°onditions() {
            this.conditionsAccepted = !this.conditionsAccepted
        },
        async submitHandler() {
            if (!this.conditionsAccepted) { return }
            const _data=JSON.parse(JSON.stringify(this.data))
            if(_data.type){
                _data.propertyType = _data.type;
                delete _data.type;
            }
            switch (this.type) {
                case 'property':
                    _data.type='realEstate'
                    break
                case 'credit':
                    _data.type='closeCredit'
                    break
                case 'deposit':
                    _data.type='target'
                    break
                case 'auto':
                    _data.type='auto'
                    break
            }
            delete _data.clientPaymentPercent
            delete _data.monthsCount
            delete _data.endTimestamp
            _data.amount=+_data.amount
            try {
                await this.$store.dispatch('createDeposit',_data)
                this.$message(localizeFilter('contract_created'))
                this.$emit('clearCache')
                this.$router.push('/cabinet/contracts')
            } catch(e) {
                if (e.message == 'Not enough funds') {
                    this.notEnoughFunds = true
                } else {
                    this.notEnoughFunds = false
                    this.$error(e)
                }
            }
        },
        goBack() {
            this.$emit('changeStep',2)
        }
    },
    mounted() {
        const {amount, clientPaymentPercent:percent } = this.data
        this.amount = amount
        this.fee = (percent*amount).toFixed(2)
        console.log(this.data)
    }
})
</script>

