<template>
    <div class="instruments-info">
        <div class="instruments-info-title-block">
            <img class="instruments-info-title-image" src="@/assets/newImages/info.png">
            <p class="instruments-info-title">{{$localizeFilter('calculator_deposit_2_title_1')}}</p>
        </div>

        <p class="instruments-text title">{{$localizeFilter('calculator_deposit_2_text_1')}}</p>
        <p class="instruments-text text">{{product}}</p>

        <p class="instruments-text title">{{$localizeFilter('calculator_deposit_2_text_2')}}</p>
        <p class="instruments-text text">{{amount}} dream</p>

        <p class="instruments-text title">{{$localizeFilter('fee')}}:</p>
        <p class="instruments-text text">{{fee}} dream</p>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: ['data'],
    data: () => ({
        product: '', amount: 0, percent: 0, fee: 0
    }),
    setup() {
        
    },
    mounted() {
        const {product, amount, clientPaymentPercent:percent} = this.data
        this.product = product
        this.amount = amount
        this.percent = percent
        this.fee = (amount * percent).toFixed(2)
    }
})
</script>

